    /**
     * 
     */
    giveHeart() {
        console.info(1111,this,this.state.show.giveHeart)
        if (this.state.show.giveHeart) {
            return
        }
        page.giveHeart(this);
        this.setState({
            show: {
                giveHeart: true
            }
        })
        if (this.alternate) { //
            this.alternate.restart();
        } else {
            this.alternate = anime({
                targets: '#heart',
                //opacity: [1, 0],
                translateY: -200,
                translateX: -20,
                duration: 2000,
                complete: function() {
                    console.info(888,this)
                    this.setState({
                        show: {
                            giveHeart: false
                        }
                    })
                }.bind(this)
            });
        }
    }

